<template>
  <div class="wrap">
      <b-navbar id="nav" toggleable="lg" type="light" variant="light">
        <b-container>
          <b-navbar-brand href="#">
              <img src="../assets/logo-50x200.png">
          </b-navbar-brand>

          <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
          <b-collapse id="nav-collapse" is-nav>
              <b-navbar-nav class="ml-auto" justified>
                  <b-nav-item href="#">Home</b-nav-item>
                  <b-nav-item href="#">Sobre</b-nav-item>
                  <b-nav-item href="#">Soluções</b-nav-item>
                  <b-nav-item href="#">Cases</b-nav-item>
                  <b-nav-item href="#">Contato</b-nav-item>
              </b-navbar-nav>

          </b-collapse>
        </b-container>
      </b-navbar>
  </div>
</template>

<script>
export default {
  name: 'NavBar',
  data () {
    return {
    }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .wrap {
   border-bottom: 2px solid #0C3351;
  }

  .navbar-light .navbar-nav .nav-link {
      color: #0C3351;
      font-family: Montserrat-Bold;
      text-transform: uppercase;
  }

  .navbar-light .navbar-nav .nav-link:hover, .navbar-light .navbar-nav .nav-link:focus {
      color: #FF6B00;
  }

  .navbar-brand img {
      max-width: 100%;
  }
  </style>



