<template>
    <b-row>
        <b-col md="12" class="clientes text">
            <b-container>
                <b-row>
                    <b-col md="12">
                        <h2 class="area-servico">
                            Empresas que já atuei
                        </h2>
                    </b-col>
                    <b-col md="12" class="servicos rounded">
                        <b-row>
                            <b-col md="2" class="item border" v-for="(item, index) in solucoes" :key="index">
                                <b-img fluid :src="item.link" alt=""></b-img>
                            </b-col>
                        </b-row>
                    </b-col>
                </b-row>
            </b-container>
        </b-col>
    </b-row>
  </template>

<script>
export default {
  name: 'Clientes',
  data () {
    return {
      solucoes: [
        {
          'titulo': 'Arquitetura de Soluções',
          'link': 'https://ilegra.com/wp-content/themes/ilegra-wp-theme/images/Logo.svg'
        },
        {
          'titulo': 'Arquitetura de Pré-Vendas',
          'link': 'https://site.getnet.com.br/wp-content/uploads/2021/08/logo-getnet.png'
        },
        {
          'titulo': 'Treinamentos In Company',
          'link': 'https://www.saqueepague.com.br/img/saque-e-pague-logo.7e43c6b3.png'
        },
        {
          'titulo': 'Arquitetura de Software',
          'link': 'https://www.deere.com.br/assets/images/deere-logo-agriculture.svg'
        },
        {
          'titulo': 'Liderança Ténica de Projeto',
          'link': 'data:image/png;base64,'
        },
        {
          'titulo': 'Inovação',
          'link': 'https://genteseguradora.com.br/wp-content/themes/gente/images/gente-seguradora-50-anos.png'
        }
      ]
    }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .clientes {
    background-color: #fff;
    padding-top: 80px;
    padding-bottom: 80px;
  }
  .servicos .item {
    margin-bottom: 10px;
    text-align: center;
  }
  .servicos .item img {
    padding-top: 30px;
    padding-bottom: 30px;
    padding-right: 20px;
    padding-left: 20px;
  }
  .servicos h3 {
    font-size: 22px;
    color: #FF6B00;
    font-weight: bold;
  }
  .servicos p {
    font-size: 16px;
  }

  .area-servico {
    font-size: 23px;
    color: #FF6B00;
    font-family: Montserrat-Bold;
    margin-top: 30px;
  }

  .text {
    color: #fff;
    font-family: Montserrat-Regular;
    font-size: 19px;
  }
  </style>



