<template>
    <b-row>
        <b-col md="12" class="solucoes text">
            <b-container>
                <b-row>
                    <b-col md="4" class="mb-4">
                        <h2 class="area-servico">
                            Soluções
                        </h2>
            Somamos diferentes expertises para mercados em constante transformação.
                    </b-col>
                    <b-col md="8" class="servicos">
                        <b-row>
                            <b-col md="4" class="item" v-for="(solucao, index) in solucoes" :key="index">
                                <h3>{{ solucao.titulo }}</h3>
                                <p>{{ solucao.descricao }}</p>
                            </b-col>
                        </b-row>
                    </b-col>
                </b-row>
                <b-container class="text-center">
                    <b-button variant="outline-primary" size="lg">Saiba Mais</b-button>
                </b-container>
                <div class="description-holder"></div>
            </b-container>
        </b-col>
    </b-row>
  </template>

<script>
export default {
  name: 'Solucoes',
  data () {
    return {
      solucoes: [
        {
          'titulo': 'Arquitetura de Soluções',
          'descricao': 'Ajudamos você a se diferenciar através do design de experiências significativas. Nossa abordagem ‘customer centric’ resulta na implementação de produtos e serviços digitais que entregam altas taxas de adoção e retenção.'
        },
        {
          'titulo': 'Arquitetura de Pré-Vendas',
          'descricao': 'Descricao 1'
        },
        {
          'titulo': 'Treinamentos In Company',
          'descricao': ''
        },
        {
          'titulo': 'Arquitetura de Software',
          'descricao': 'Descricao 1'
        },
        {
          'titulo': 'Liderança Ténica de Projeto',
          'descricao': 'Descricao 1'
        },
        {
          'titulo': 'Inovação',
          'descricao': 'Descricao 1'
        }
      ]
    }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .solucoes {
    background-color: #FF6B00;
    padding-top: 130px;
    padding-bottom: 130px;
  }
  .servicos .item {
    margin-bottom: 70px;
  }
  .servicos h3 {
    font-size: 22px;
    color: #0C3351;
    font-weight: bold;
  }
  .servicos p {
    font-size: 16px;
  }

  .area-servico {
    font-size: 60px;
    color: #0C3351;
    font-family: Montserrat-Bold;
  }

  .text {
    color: #fff;
    font-family: Montserrat-Regular;
    font-size: 19px;
  }
  .description-holder {
    width: 100%;
    padding: 30px 100px;
    border: 2px solid #FFD100;
    border-top: none;
    display: block;
    position: relative;
  }

  .btn-outline-primary {
    color: #0C3351;
    border-color: #0C3351;
  }
  .btn-outline-primary:hover {
    color: #fff;
    background-color: #0C3351;
    border-color: #0C3351;
  }
  </style>



