<template>
    <b-row>
        <b-col md="12" class="stack text">
            <b-container>
                <b-row>
                    <b-col md="4">
                        <h2 class="area-servico">
                            Stack Tecnológica
                        </h2>
                    </b-col>
                    <b-col md="8" class="servicos">
                        <b-row>
                            <b-col md="2" class="item" v-for="(solucao, index) in solucoes" :key="index">
                                <b-img fluid-grow src="https://ilegra.com/wp-content/uploads/2020/02/Logo-GitHub-pb.png" alt=""></b-img>
                            </b-col>
                        </b-row>
                    </b-col>
                </b-row>
            </b-container>
        </b-col>
    </b-row>
  </template>

<script>
export default {
  name: 'Stack',
  data () {
    return {
      solucoes: [
        {
          'titulo': 'Arquitetura de Soluções',
          'descricao': 'Ajudamos você a se diferenciar através do design de experiências significativas. Nossa abordagem ‘customer centric’ resulta na implementação de produtos e serviços digitais que entregam altas taxas de adoção e retenção.'
        },
        {
          'titulo': 'Arquitetura de Pré-Vendas',
          'descricao': 'Descricao 1'
        },
        {
          'titulo': 'Treinamentos In Company',
          'descricao': ''
        },
        {
          'titulo': 'Arquitetura de Software',
          'descricao': 'Descricao 1'
        },
        {
          'titulo': 'Liderança Ténica de Projeto',
          'descricao': 'Descricao 1'
        },
        {
          'titulo': 'Inovação',
          'descricao': 'Descricao 1'
        }
      ]
    }
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .stack {
    background-color: #fff;
    padding-top: 80px;
    padding-bottom: 80px;
  }
  .servicos .item {
    margin-bottom: 0px;
  }

  .servicos h3 {
    font-size: 22px;
    color: #0C3351;
    font-weight: bold;
  }
  .servicos p {
    font-size: 16px;
  }

  .area-servico {
    font-size: 23px;
    color: #0C3351;
    font-family: Montserrat-Bold;
    margin-top: 30px;
  }

  .text {
    color: #fff;
    font-family: Montserrat-Regular;
    font-size: 19px;
  }
  </style>



